.overlay {
  position: absolute; 
  top: 20%; 
  width: 100%;
  transition: 1s ease-in;
  opacity:0;
  color: white;
  text-align: center;
}

.image:hover .overlay{
  opacity:1;
}


.image:hover .projectImage{
  transition: 1s ease-in;
  filter:blur(5px);
  opacity:.4;
}

.image:hover .hover_on_link{
  border:solid #ccac9d 1px;
  border-radius:10px;
}

.hover_on_link:hover{
    background:#ccac9d;
}
@media(max-width:600px){
  .projectImage{
    width:325px;
    height:250px;
  }
 
}
@media(max-width:376px){
  .projectImage{
    width:325px;
    height:230px;
  }
 
}


